import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
    state:{
        products: [
            {name:'马云',price:200},
            {name:'马化腾',price:140},
            {name:'官安安',price:20},
            {name:'韩寒',price:10},
        ]
    },
    getters:{
        saleProducts: state => {
            let saleProducts = state.products.map(product=> {
                return {
                    name: `**${product.name}**`,
                    price: product.price / 2
                }
            })
            return saleProducts
        }
    },
    mutations:{
        reductPrice: (state,payload) => {
            // setTimeout(function(){
                state.products.forEach(product => {
                    product.price -= payload
                });
            // },3000)
        }
    },
    actions:{
        reductPrices: (context,payload) => {
            setTimeout(function(){
                context.commit('reductPrice',payload)
            },1000)
        }
    }
})


